@import './../modules';

header{
  transition: background-color 0.3s;
  position: fixed;  
  width: 100%;
  z-index: 999;
  div.gnb-depth1{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8.4rem;
    .--left{
      display: inline-flex;
    }

    .gnb-logo{
      height: 8.4rem;
      display: inline-block;
      width: 14rem;
      .logo{
        height: 100%;
      }
      .logo::after{
        display: none;
      }
    }

    ul{
      display: inline-flex;
      align-items: center;
      a{
        font-weight: 500;
      }
    }

    ul.gnb-sitemap{
      margin-left: 5.8rem;
      gap: 5.8rem;
      li:last-child{
        display: none;
      }
    }
    ul.gnb-community{
      gap: 5.8rem;
      margin-right: 6.3rem;
    }
    ul.gnb-user{
      gap: 2rem;
      a{
        @include style_font(400, 1.4rem, 1, #787878);
        margin: 0;
        &::after{
          display: none;
        }
        &.focused, &:hover{
          color: #666;
          font-weight: 500;
        }
      }
    }
  }
  div.gnb-depth2{
    width: 100%;
    position: fixed;
    .depth2{
      color: $font_color-white;
      background-color: $color-10319D_royalazure;
      .width1440{
        display: flex;
        padding: 7.6rem 0;
        height: 38rem;
        div{
          margin-right: 239px;
          h5{
            padding-left: 10px;
            font-weight: 700;
            font-size: 24px;
            padding-bottom: 0.5em;
          }
          p{
            width: 266px;
            height: 15rem;
            padding-left: 10px;
            padding-bottom: 3em;
            @include style_font(300, 1.8rem, 1.8em, white);
          }
          button{
            width: 208px;
            height: 47px;
            background: #FFFFFF;
            border-radius: 5rem;
            font-weight: 700;
            color: $color-10319D_royalazure;
          }
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          li{
            width: 386px;
            padding-bottom: 1em;
            margin: 20px 48px 40px 0;
            
            font-weight: 700;
            border-bottom: 1px solid rgba(255, 255, 255, 0.07);
            a{
              color: $font_color-white;
              
            }
          }
        }
      }
    }
    .depth2:last-child{
      background-color: $color-008CB2-commu-03;
    }
  }
}

.depth2{
  display: none;
}
.depth2-open{
  display: block;
  @include animation(0s, 0.6s, fadeIn);
}

// ! 이거 지금 안되고 있음 
.depth2-close{
  @include animation(0s, 0.3s, fadeOut);
}
.depth2-none{
  display: none;
}

// main과 sub페이지는 header 색상들이 다름.
header.header-main{
  @include style_header("main");
}
header.header-sub{
  @include style_header("sub");
}