.wrap-content.promotion-wordView{
  background-color: #F8F9FA;
  padding: 96px 0;

  .view{
    background-color: #fff;
    padding: 58px 70px 70px;

    .title{
      font-size: 28px;
      font-weight: bold;
      text-align: center;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      &::before{
        // background-image: url('../../public//assets/images/ico_quotes.svg');
        content:'';
        background-repeat: no-repeat;
        display: block;
        width: 30px;
        height: 30px;
        margin-bottom: 38px;
      }
    }

    .date{
      color: #797979;
      display: block;
      text-align: center;
      margin-top: 20px;
      margin-bottom: 48px;
    }

    hr.line{
      background-color: #D8DCDE;
      height: 2px; 
    }

    .content{
      padding: 48px 0;
      font-size: 15px;
      line-height: 30px;
      color: #3C3C3C;

      .wrap-img{
        margin-bottom: 48px;
      }
    }
  }

  .btn-nextList{
    color: #2C99FF;
    font-size: 18px;
    margin-top: 56px;
    line-height: 32px;
    display: flex;
    justify-content: center;
    width: 100%;
    
    &::after{
      // background: url('../../public//assets/images/ico_next.svg') no-repeat;
      content: '';
      width: 32px;
      height: 32px;
      display: block;
      margin-left: 12px;
    }
  } 
}