// 🏠️ OSP-7121

@import '../../modules';


main.main-osp7121{
  position: relative;
  section{
    padding-top: 9.6rem;
    padding-bottom: 16.7rem
  }
  hgroup{
    @include style_font(700, 3.2rem, 1, #2e2e2e);
    padding-bottom: 9.6rem;
    border-bottom: 3px solid $line_color-ECECEC_grey;
    strong{
      color: $color-2C99FF_azure;
    }
    h2{
      padding-bottom: 2rem;
    }
  }

  div.form-layout{
    padding-top: 6.4rem;
    padding-bottom: 9.4rem;
    text-align: center;
    ul.form-ul{
      text-align: left;
      display: flex;
      gap: 1.6rem;
      flex-direction: column;
      li{
        position: relative;
        select {
          appearance: none;
          
        }

        label,
        textarea::placeholder,
        input::placeholder{
          @include style_font(500, 1.6rem, 1, #91989D);
        }

        label{
          transition: all 0.3s;
          position: absolute;
          top: 2.1rem;
          left: 6.8rem;
          user-select: none;
        }

        > div, 
        > input, 
        select, 
        textarea{
          @include style_font(500, 1.6rem, 1, #91989D);
          width: 25%;
          height: 5.8rem;
          background: #FBFBFB;
          border: none;
          outline: 1px solid #D8D8D8;
          border-radius: 10px;
          padding: 1.6rem 2rem;
          margin-left: 5rem;
          // 가로 100
          &.full{
            width: 100%;
            &.cont{
              vertical-align: text-top;
              height: 50rem;
            }
          }
        }
        // 타이핑하고 있을 때,, 
        &.typed.--t01{ // type1
          label{
            transform: translateY(-1rem);
            visibility: visible;
            font-size: 1.2rem;
          }
          input{
            padding-top: 3rem;
            padding-bottom: 1rem;
            color: #212121;
            font-size: 1.6rem;
          }
          // input:invalid{
          //   // outline: 1px solid #FF9C9C;
          // }
        }
        &.typed.--t02{ // type2 (타입2: 디자인 규칙)
          input,
          textarea{
            color: #212121;
          }
        }
        // focus CSS
        div.checked, 
        input:focus, 
        textarea:focus{
          outline: 2px solid $color-2C99FF_azure;
        }
        // list style (파란 점)
        &::after{
          position: absolute;
          content: '';
          top: 2.8rem;
          left: 2rem;
          width: 8px;
          height: 8px;
          background-color: $color-2C99FF_azure;
          border-radius: 50%;
        }
        // checkbox 
        div.checkbox{
          input[type="checkbox"]{
            appearance: none;
            width: 2.4rem;
            height: 2.4rem;
            border: 1px solid #D8D8D8;
            border-radius: 3px;
            &:checked{
              transition: all 0.3s;
              background-color: $color-2C99FF_azure;
              border: 1px solid $color-2C99FF_azure;
              background-image: url('../../../../public/asset/images/icon/ico_CheckBox.svg');
              background-size: cover;
              background-position: center;
            }
          }
          span{
            vertical-align: text-top;
            margin-left: 1rem;
          }
        }

      }
    }
    input[type="submit"]{
      width: 32rem; 
      height: 7.6rem;
      margin-top: 7.6rem;
      margin-bottom: 9.3rem;
      @include style_font(700, 2.4rem, 1, white);
    }
  }
  div.inquiry{
    border-top: 4px solid #ececec;
    padding-top: 4rem;
    text-align: center;
    span{
      @include style_font(400, 1.8rem, 1, #666);
    }
    span.title{
      font-weight: 700;
      padding-right: 1.4rem;
    }
    span:nth-child(2){
      padding-right: 5.2rem;
    }
  }

  section.result-bg{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);

    display: flex;
    justify-content: center;
    div.question-result{
      width: 42.4rem;
      height: 43.7rem;
      margin-top: 24rem;
      padding: 3rem;

      text-align: center;

      background-color: white;
      border-radius: 2rem;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
      div{text-align: right;}
      i.icon-mail{
        margin-top: 2.2rem;
        margin-bottom: 3.5rem;
        width: 8rem;
        height: 8.4rem;
      }
      h6{
        margin-bottom: 3.2rem;
        @include style_font(700, 2rem, 1, #3c3c3c);
      }
      p{
        margin-bottom: 6.6rem;
        @include style_font(400, 1.6rem, 1.6em, #666);
        strong{
          color: $color-2C99FF_azure;
        }
      }
      button{
        font-weight: 700;
        color: $color-2C99FF_azure;
        &.confirm{
          margin-right: 0.4rem;
        }
      }
    }
  }
  section.result-bg.in-active{
    display: none;
  }
  section.result-bg.active{
    display: flex;
  }
}