/**
**🍈️ mixin()
** style
** animation
*/

@mixin style_background-01(){
  position: relative;
  padding-top: 9.6rem;
  padding-bottom: 20.5rem;
  width: 100%;
  background-color: $bg_main-F8F9FA;
  z-index: -2;

  .bg_c-l, .bg_c-r{
    position: absolute;
    top: 0;
    height: 100%;
    background-repeat: no-repeat;
    z-index: -1;
  }
  .bg_c-l{
    width: 68.5rem;
    width: 35%;
    left: 0;
    background-position-x: right;
    background-position-y: 200%;
    background-image: url('../../public/asset/images/res/bg_circle_1.svg');
  }
  .bg_c-r{
    width: 61rem;
    width: 30%;
    right: 0;
    background-position-x: left;
    background-position-y: 10%;
    background-image: url('../../public/asset/images/res/bg_circle_2.svg');
  }

}
@mixin style_header($class_name){
  @if $class_name == 'main'{
    background-color: transparent;
    div.gnb-depth1{
      ul.gnb-sitemap li a,
      ul.gnb-community li a{
        @include style_header_gnb_a(white, 0.3s);
        color: white;
      }
      ul.gnb-community svg path{
        fill: white;
      } 
      ul.gnb-user a{
        color: rgba(255, 255, 255, 0.58);
        &:hover{
          color: rgba(255, 255, 255, 1);
        }
      }
    }
    .logo{
      background-image: url('../../public/asset/images/icon/logo_white.svg');
    }
    
  }@else if $class_name == 'sub'{
    background-color: white;
    div.gnb-depth1 {
      a{
        @include style_header_gnb_a($font_color-black, 0.3s, $color-3855FF_ultramarine);
      }
      ul.gnb-community{
        li:first-child a{
          &.focused, &:hover{
            @include style_header_gnb_a($font_color-black, 0.3s, $color-008CB2-commu-03);
          }
          &::after{
            background-color: $color-008CB2-commu-03;
          }
        }
        li:last-child a{
          svg path{
            fill: #212121;
          }
          &:hover svg path{
            transition: all 0.3s;
            fill: $color-3855FF_ultramarine;
          }
        }
      }
    }
    .logo{
      background-image: url('../../public/asset/images/icon/logo_blue.svg');
    }
  }
}
@mixin style_font($weight: 400, $size: 18px, $line_height: 1, $color: black){
  font-weight: $weight;
  font-size: $size;
  line-height: $line_height;
  color: $color;
}
@mixin style_main_h3($color:black){
  h3{
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 1;
    color: $color;
    div{
      height: 36px;
      &:first-child{
        margin-bottom: 20px;
      }
    }
    strong{
      color: $color-2C99FF_azure;
    }
  }
}

@mixin make_line($width, $height, $color){
  position: relative;
  &::after{
    position: absolute;
// 만드는 중 
    bottom: 0;
    left: 49%;

    content: '';
    width: $width;
    height: $height;
    background: $color;
  }
}

@mixin grow_fontw($color, $time){
  text-shadow: 0 0 0 rgba($color, 0);
  transition: text-shadow $time ease-out;
  &.focused, &:hover{
    color: $color;
    text-shadow: 
    0 -0.2px 0 $color,
    -0.2px 0 0 $color,
    0.2px 0 0 $color;
  }
}

@mixin board_grid_notice(){
  display: grid;
  grid-template-columns: 10% 78.5% 10%;
  align-items: center;
  gap: 8px;
  .cate{
    @include style_font(500, 1.4rem, 1, black);
    padding: 1.1rem 2.2rem;
    background-color: #eaeaea;
    border-radius: 3rem;
    &.--pr{
      background-color: #DCE1FF;
    }
  }
}

@mixin board_grid_question(){
  display: grid;
  grid-template-columns: 10% 89%;
  grid-template-areas: 'tit tit' 'cnt cnt';
  align-items: center;
  gap: 8px;
  .cate{
    background-color: #CFE9FD;
    &.--grsd{
      background-color: #CCF7F7;
    }
    &.--srds{
      background-color: #D3FFDF;
    }
    &.--etc{
      background-color: #EAEAEA;
    }
  }
}

@mixin style_header_gnb_a($color, $time, $hover_color:$color){
  position: relative;
  padding: 0.5rem 0; 
  margin: 0 0.5rem;
  height: 2rem;
  transition: all 0.3s;
  color: $color;

  text-shadow: 0 0 0 rgba($hover_color, 0);
  transition: text-shadow $time ease-out;
  
  &::after{
    position: absolute;
    content: '';
    bottom: 0;
    right: 0;
    display: block;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    transform-origin: left;

    background-color: $hover_color;
  }
  &.focused::after {
    visibility: hidden;
  }
  &.focused:hover::after, &:hover::after{
    visibility: visible;
    transform: scaleX(1);
    transition: transform 0.2s ease-in;
  }

  &.focused, &:hover{
    color: $hover_color;
    text-shadow: 
    0 -0.1px 0 $hover_color,
    -0.1px 0 0 $hover_color,
    0.1px 0 0 $hover_color;
  }
}

@mixin icon_point_before($top:-2.8rem, $left: -2.8rem){
  // osp4111
  position: relative;
  &::before{
    position: absolute;
    content: '';
    top: $top;
    left: $left;
    width: 3rem;
    height: 3rem;
    background-image: url('../../public/asset/images/icon/ico_subtract.svg');
  }
}

// ------------------------------------------
@mixin animation ($delay, $duration, $name) {
  animation-fill-mode: forwards;

  animation-delay: $delay;
  animation-duration: $duration;
  animation-name: $name
}
@mixin keyframe ($animation_name) {
  @keyframes #{$animation_name} {
      @content;
  }
}

@include keyframe (moveUp){
  from{
    opacity: 0.3;
    transform: translateY(5px);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}
@include keyframe (moveDown){
  from{
    opacity: 0.3;
    transform: translateY(-3px);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}
@include keyframe (fadeIn){
  from{
    opacity: 0;
  }to{
    opacity: 1;
  }
}
@include keyframe (fadeOut){
  from{
    opacity: 1;
  }to{
    opacity: 0;
  }
}

