/**
** Board - Board - section.main-board
** Board - News - section.main-news
** Board - Customer - section.main-customer

** Board - notice 고객지원 게시판 
*/
@import '../modules';

section.main-board{
  .width1440{
    height: 815px;
    text-align: center;
    padding: 9.5rem 0;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @include style_main_h3($font_color-black);
  div.board-cards{
    display: flex;
    height: 289px;
    justify-content: space-between;
    button{
      position: relative;
      width: 335px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;

      text-align: left;
      img{
        width: 100%;
        height: 195px;
        border-radius: 1rem;

        background-color: dimgray;
      }
      .board-title{
        font-size: 20px;
        font-weight: 500;
        line-height: 150%;
        color: #000000;
      }
    }
  }
}

section.main-news{
  @include style_main_h3($font_color-black);

  background: $bg_main-news;
  .width1440{
    padding: 9.6rem 0 9rem 0;
    height: 1128px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 7.6rem 10rem;
  }
  div.news-grid{
    position: relative;
    height: 683px;

    display: grid;
    grid-template-columns: 830px 577px;

    column-gap: 3.2rem;
    row-gap: 3rem;
    button{
      text-align: left;
      border-radius: 10px;
      background-color: white;
      .news-content{
        padding: 4rem;
        .news-title{
          height: 77px;
          font-weight: 500;
          font-size: 24px;
          line-height: 150%;
          color: $font_color-black;
          margin-bottom: 20px;

          // 두 줄부터 ... 해야해용
          // white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .news-date{
          line-height: 170%;
          color: $font_color-B0B0B0_grey;
          margin-top: 14px;
          font-weight: 300;
        }        
      }
    }
    .--col1{
      display: flex;
      flex-direction: column;
      justify-content: space-between;        
      height: 100%;
      grid-row-start: 1;
      grid-row-end: 4;
      img{
        width: 830px;
        height: 439px;
        border-radius: 10px;
        background-color: darkgray;
      }
      .news-content{
        position: relative;
        height: 244px;
        padding: 4rem;
        p{
          line-height: 170%;
          width: 100%;
          letter-spacing: -0.5px;
        }
        .news-title{
          height: 55px;
          font-weight: 700;
          font-size: 26px;
          color: $font_color-black;
          margin-bottom: 5px;
        }
      }
    }
  }
}

section.main-customer{
  .width1440{
    position: relative;
    height: 35.7rem;
  }
  .customer-banner{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 7rem 16rem;
    .customer-title{
      padding: 1em 8.6rem;
      color: $font_color-white;
      h5{
        font-weight: 700;
        font-size: 36px;
        padding-bottom: 2.4rem;
      }
      p{
        font-weight: 400;
        font-size: 24px;
      }
    }
    .btn-customer{
      width: 280px;
      height: 76px;

      background: #FFFFFF;
      border-radius: 48px;
      color: $color-2C99FF_azure;
      font-weight: 700;
      font-size: 24px;
    }
  }
  .--banner{
    position: absolute;
    bottom: 0;      
    
    width: 100%;
    height: 299px;
    background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);
    border-radius: 30px;
  }
}

// 고객지원 게시판
div.custom-board{
  padding: 1.6rem 0 3.8rem 0;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  background-color: white;
  border-radius: 3rem;
  ul.cont-ul{
    padding: 0 4.4rem;
    li{
      @include board_grid_notice();
      border-bottom: 1px solid #ececec;
      text-align: center;
      a.cont{
        @include style_font(700, 2rem, 1, #212121);
        position: relative;
        display: inline-block;
        text-align: left;
        padding: 3rem 0;
        margin-left: 15rem;
        .icon-table_new{
          margin-left: 6px;
        }
      }
      a.cont.notice::before{
        position: absolute;
        content: '';
        top: 1.2em;
        left: -5rem;
        width: 2.8rem;
        height: 2.8rem;
        vertical-align: middle;
        background-image: url('../../../public/asset/images/icon/ico_table_notice.svg');
      }
      .date{
        @include style_font(400, 1.8rem, 1, #797979);
        padding-left: 1em;
      }
    }
  }
  ul.cont-ul.notice{
    margin-bottom: 4.8rem;
  }
  ul.cont-ul.question{
    padding-top: 3.6rem;
    li{
      position: relative;
      @include board_grid_question();

      overflow: hidden;
      .cont{
        display: inline-flex;
        justify-content: space-between;
      }
      div.question-board{
        grid-area: cnt;
        text-align: left;
        padding: 4.4rem 4.8rem 3.2rem 4.8rem;
        background-color: $bg_main-F8F9FA;
        border-radius: 0 0 3rem 3rem;
        p{
          margin-bottom: 6.4rem;
          @include style_font(400, 2rem, 2em, #4b4b4b);
        }
        div.no-res{
          >span{
            @include style_font(400, 1.4rem, 1, $color-2C99FF_azure);
          }
          width: 32rem;
          margin-left: auto;
        }
      }
    }
    li.--dp2{
      display: none;
    }
    li.--dp2.active{
      display: block;
    }

  }
}
// 구름 커뮤니티 게시판 
div.board-frame{
  padding: 4.6rem;
  background: white;
  box-shadow: 0px 15px 40px rgba(45, 52, 74, 0.05);
  border-radius: 30px;
  text-align: center;
  ul{
    li{
      padding: 2.1rem 2rem 2.1rem 1rem;
      display: grid;
      grid-template-columns: 91.2rem 13.6rem 15.2rem 12.4rem;
      align-items: center;
      vertical-align: middle;
      gap: 8px;

      border-bottom: 1px solid #ececec;
      a{
        display: flex;
        align-items: center;
        padding: 1rem 0;
        text-align: left;
        h4{
          padding-left: 2.5rem;
          padding-right: 8px;
          @include style_font(500, 2rem, 1, $font_color-212121_grey);
        }
        span.views{
          margin-right: 6px;
          color: $color-0AB4E2_commu-01;
        }
        .icon-img,
        .icon-file,
        .icon-zip,
        .icon-new{
          width: 2.4rem;
          height: 2.4rem;
          margin: 0 2px;
        }
      }
    }
    li.board-title {
      padding: 0;
      border-bottom: none;
      span{
        height: 4rem;
        padding: 1.2rem;
        background: $bg_color-F8F8F8-grey;
        @include style_font(500, 1.6rem, 1, $font_color-797979_grey);
        border-radius: 5px;
        i.icon-sort{
          vertical-align: top;
          height: 1.6rem;
          margin-left: 6px;
        }
      }
    }

  }
  a.linkto{
    display: inline-block;
    margin-top: 3rem;
    padding-top: 1.8rem;
    @include style_font(500, 1.8rem, 1, $color-008CB2-commu-03);
  }
}

// 구름 커뮤니티 게시판 상세 페이지
section.board-detail{
  width: 1440px;
  margin: 0 auto;
  padding: 4.4rem;
  box-shadow: 0px 15px 40px rgba(45, 52, 74, 0.1);
  border-radius: 3rem;

  i.icon{
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    background-color: rgba(213, 213, 213, 0.408);
  }
  span{
    @include style_font(400, 1.6rem, 1, #B0B0B0);
    letter-spacing: 0;
  }
  strong{
    color: $color-0AB4E2_commu-01;
  }
  p{
    @include style_font(400, 1.6rem, 2.2em, #4b4b4b);
    letter-spacing: 0;
  }
  div.attch-infor{
    button.togg-attch span{
      margin: 0 1rem;
    }
  }
  ul.attch-list{
    margin-top: 1rem;
    display: flex;
    gap: 1.4rem;
    li{
      padding: 0.5rem 1rem;
      background-color: #E7EBEE;
      border-radius: 6px;
    }
    a.down-attch{
      i.icon{margin-right: 1rem;}
      span{color: #47B7D6;}
    }
    i.icon-close{
      margin-left: 1rem;
    }
  }

  div.cont{
    div.cont-tit{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      padding-top: 1rem;
      padding-bottom: 2.4rem;
      margin: 0 2rem;
      .--l{
        position: relative;
        padding-left: 4.8rem;
        h4{
          i.icon-table-notice{
            position: absolute;
            left: 0;
            width: 2.8rem;
            height: 2.8rem;
          }
          @include style_font(700, 2rem, 1, $font_color-212121_grey);
          padding-bottom: 1rem;
        }
        div{
          padding-top: 1rem;
          span{
            padding-left: 2rem;
          }
          span.auth{
            position: relative;
            padding-right: 2rem;
            padding-left: 0;
            i.icon{
              width: 2.4rem;
              height: 2.4rem;
              vertical-align: middle;
              margin-right: 6px;
              background-color: rebeccapurple;
            }
            &::after{
              position: absolute;
              top: 0.2em;
              right: 0;
              content: '';
              width: 1px;
              height: 1em;
              background-color: #B0B0B0;
            }
          }
        }
      }
      .--r{
        text-align: right;
        div:first-child span{
          color: $color-0AB4E2_commu-01;
        }
        div{
          padding-bottom: 1rem;
          span{
            margin-left: 2rem;
          }
          i.icon{
            background-color: red;
            margin-right: 1rem;
          }
        }
      }
    }
    div.cont-detail{
      border-top: 1px solid #ECECEC;
      padding: 4.4rem 4.8rem;
      background-color: $bg_main-F8F9FA;
      p{

        margin-bottom: 4.4rem;
      }
    }
  }
  div.commt{
    div.commt-tit{
      margin-top: 1.9rem;
      padding: 2.8rem 2rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #ECECEC;
      h4{
        @include style_font(400, 2.4rem, 1 ,$font_color-212121_grey);
      }
      div{
        span{
          margin-right: 1rem;
        }
        button.commt-alarm-toggle{
          vertical-align: middle;
          position: relative;
          width: 3.6rem;
          height: 2rem;
          padding: 0.33rem;
          border-radius: 2rem;
          background-color: #B0B0B0;
          transition: background-color 0.3s;
          &::after{
            position: absolute;
            content: '';
            top: 0.33rem;
            left: 0.33rem;
            width: 1.33rem;
            height: 1.33rem;
            border-radius: 50%;
            background-color: white;
            transform: translateX(0);
            transition: transform 0.3s;
          }
          &.active {
            background-color: #0AB4E2;
            &::after{
              transform: translateX(120%);
            }
          }
        }
      }
    }

    div.commt-inf{
      padding-top: 1.4rem;
      padding-bottom: 4.4rem;
      button.btn-update-commt{
        margin-left: 2rem;
        span{
          color: #47B7D6;
          i.icon{
            margin-right: 1rem;
          }
        }
      } 
    }

    ol.commt-dep1{
      position: relative;
      padding-top: 2.4rem;
      padding-right: 2rem;
      margin-bottom: 2.4rem;
      > li{
        > div{
          padding-left: 5rem;
        }
        span.commt-auth{
          position: relative;
          @include style_font(700, 1.8rem, 1, #666666);
          margin-bottom: 1.4rem;
          i.icon{
            position: absolute;
            left: -3.2rem;
            width: 2.4rem;
            height: 2.4rem;
          }
        }
      }
    }

    ol.commt-dep2{
      position: relative;
      padding-left: calc(2.8rem + 5rem);
      padding-right: 4.4rem;
      padding-top: 4.4rem;
      background-color: $bg_main-F8F9FA;
      span.commt-auth{
        span.is-auther{
          padding: 4px 7px;
          color: white;
          font-size: 1.4rem;
          background-color: #F86848;
          border-radius: 5rem;
          margin-left: 6px;
        }
      }
      p{
        padding-bottom: 4.4rem;
      }
      button.box-toggle{
        position: absolute;
        right: 4.4rem;
        i.icon{
          width: 2.4rem;
          height: 2.4rem;
        }
      }
      div.attch-infor{
        margin-bottom: 3.4rem;
      }
    }
  }
  div.commt-update{
    padding: 4.4rem 4.8rem;
    border: 1px solid #DCDDDE;
    span.user-n{
      @include style_font(700, 1.8rem, 1, #666666);
      margin-bottom: 2rem;
    }
    textarea{
      position: relative;
      width: 100%;
      padding: 2rem;
      @include style_font(400, 1.6rem, 1, #91989D);
      border: 1px solid #D8D8D8;
      background-color: #FBFBFB;
      border-radius: 1rem;
      resize: vertical;
    }
    div.commt-more{
      padding-top: 2.4rem;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      div:first-child{
        button{
          padding: 1rem 1.3rem;
          align-items: center;
          gap: 4px;
          font-size: 15px;
          margin-right: 2rem;
        }
        > span{
          vertical-align: text-top;
          color: #666666;
        }
      }
      div:last-child{
        text-align: right;
        span{
          display: block;
          margin-bottom: 2.5rem;
        }
      }
    }
  }


}
::-webkit-resizer{
  display: block;
  width: 10000px;
  position: absolute;
  left: 50px;
  background-color: red;
}

div.pagination{
  display: flex;
  justify-content: center;
  button.ar{
    width: 2rem;
    height: 3rem;
    &.--l{
      transform: rotate(180deg);
    }
  }
  ol{
    display: inline-flex;
    align-items: center;
    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;
    }
    li.now button{
      color: $color-2C99FF_azure;
      font-weight: 700;
    }
  }
}
div.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $color-0AB4E2_commu-01;
  }
}