/** 
  * 2022.03.21 ~ 
  ** 🛠 reset-scss
  ** @import modules
  ** @import board << layout component's style 
  ** 🍚️ theme
  ** 💫️ icons
  ** 🎈 illust
*/
@import 'scss-reset/_reset.scss';

@import 'modules'; // *modules: 변수, mixin 패키지

@import './layout/board';

// ? 뭔가 안되네용 ㅠㅠ 😥 
// @include font-face('NotoSansCJKkr-Regular', 'NotoSansCJKkr-Regular', 400);
// $font-stack: NotoSans, sans-serif;

::-webkit-scrollbar{
  width: 0;
  height: 0;
}
html{
  font-size: 10px; // rem 기준
  scroll-behavior: smooth;
}
// * normalize
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  // font: 100% $font-stack;
  letter-spacing: -0.5px;
  color: #2E2E2E;
  line-height: 1;
  word-break: keep-all;
  
  min-width: 1440px;

  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
  input{
    border: 0;
  }
  textarea{
    resize: none;
  }
}

// *🍚️ theme
.App{
  position: relative;
  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }    
  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }
  // * global 정수진C 
  .--h{
    visibility: hidden;
  }
  .--n{
    display: none;
  }
  // * 버튼 - basic theme
  .btn{
    transition: all 0.3s;
  }
  .btn-basic-01{
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    &.--d{
      color: $font_color-B0B0B0_grey;
      border: 1px solid $font_color-B0B0B0_grey;
    }
    &.--a{
      color: #666666;
      border: 1px solid #666666;
      &:active{
        color: #212121;
        border: 1px solid #212121;
      }
    }
  }
  .btn-basic-02{
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    display: inline-flex;
    &.--d{
      background-color: #B0B0B0;
      color: white;
    }
    &.--a{
      color: white;
      background-color: $color-0AB4E2_commu-01;
      &:hover{
        background-color: $color-06A1CB-commu-02;
      }
      &:active{
        background-color: $color-008CB2-commu-03;
      }
    }

  }
  .btn-rounded-01{
    @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
    padding: 1.5rem 3rem 1rem 3rem;
    background: white;
    border: 1px solid $color-2C99FF_azure;
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: $color-2C99FF_azure;
      }
    }
    &:hover{
      color: $color-007DF1_azure-emp-01;
      border: 1px solid $color-007DF1_azure-emp-01;
      .icon-down-svg svg path{
        fill: $color-007DF1_azure-emp-01;
      }
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
      border: 1px solid $color-0063C0_azure-emp-02;
      .icon-down-svg svg path{
        fill: $color-0063C0_azure-emp-02;
      }
    }

  }
  .btn-rounded-02{
    @include button_set('basic-rounded-02');
    padding: 1.6rem 6.5rem;
    position: absolute;
    top: -10rem;
    right: 50rem;
  }
  .btn-rounded-03{
    @include style_font(500, 1.8rem, 1.2, white);
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: white;
      }
    }
  }
  .btn-link{
    display: inline-block;
    color: $color-2C99FF_azure;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1rem;
  
    text-align: center;
  }
  .btn-more{ 
    color: $color-0082F3_btn;
  }
  // * 버튼 - commu theme
  .btn.commu{
    display: inline-block;
    // height: 4.7rem;
    padding: 1.2rem 3rem 1rem 3rem;
    border-radius: 4rem;
  }
  .btn-basic-02.commu{
    &.--a{
      background-color: $color-0AB4E2_commu-01;
      &:hover{
        background-color: $color-06A1CB-commu-02;
      }
      &:active{
        background-color: $color-008CB2-commu-03;
      }
    }

  }
  .btn-rounded-01.commu{
    @include style_font(500, 1.8rem, 1, $color-0AB4E2_commu-01);
    background: #FFFFFF;
    border: 1px solid $color-0AB4E2_commu-01;
    border-radius: 4rem;
    .icon-down{
      vertical-align: middle;
      width: 2.4rem;
      height: 2.4rem;
      background-size: cover;
    }
    &:hover{
      color: #06A1CB;
      border: 1px solid #06A1CB;
    }
    &:active{
      color: $color-008CB2-commu-03;
      border: 1px solid $color-008CB2-commu-03;
    }
  }
  .btn-rounded-03.commu{
    @include style_font(500, 1.8rem, 1.2, white);
    padding: 1.5rem 3rem;
    background: none;
    background-color: $color-0AB4E2_commu-01;
    
    border-radius: 4rem;
    &:hover{
      background: #06A1CB;
    }
    &:active{
      background: $color-008CB2-commu-03;
    }
  }

  // * search table
  .search-table{
    position: relative;
    margin-left: auto;
    width: 35.7rem;
    label{
      position: absolute;
      top: 1.3rem;
      left: 1.6rem;
      .icon-search{
        width: 2.2rem;
        height: 2.2rem;
        &.focused svg path{
          fill: $color-2C99FF_azure;
        }
      }
      
    }
    input[name='search-table']{
      width: 100%;
      padding: 1.2rem 0 1.2rem 4.7rem;

      color: #91989D;
      font-size: 1.6rem;
      
      border: 1px solid #D8D8D8;
      border-radius: 1rem;
      background: #FBFBFB;
      transition: border 0.3s;
      &.typing, &:focus{
        border: 1px solid $color-2C99FF_azure;
      }
    }
    button.btn-close{
      position: absolute;
      top: 1.4rem;
      right: 2rem;
      width: 2rem;
      height: 2rem;
    }
  }
  .search-table.commu{
    input[name="search-table"]{
      &:focus{
        border: 1px solid $color-0AB4E2_commu-01;
        @include style_font(700, 1.6rem, 1, $font_color-212121_grey);
      }
    }
  }

  // * search-banner 
  .search-banner{
    position: absolute;
    top: -15.6rem;
    right: 27rem;
    
    z-index: 99;
    label{
      position: absolute;
      top: 1.6rem;
      left: 1.6rem;
      .icon-search{
        padding: 0.3rem;
        width: 2.8rem;
        height: 2.8rem;
        margin-right: 1.2rem;
        svg{
          width: 2.2rem;
        }
      }
    }
    label .icon-search.focused svg path{
      fill: $color-2C99FF_azure;
    }
    input[name="search"]{
      width: 44.9rem;
      height: 5.8rem;
      padding: 1.6rem 2rem 1.6rem 6.1rem;
      border-radius: 10rem;

      color: #91989D;
      transition: all 0.3s;
      outline: 3px solid rgba(16, 49, 157, 0);
      &:hover{
        outline: 3px solid rgba(16, 49, 157, 0.1);
      }
      &:focus{
        outline: 3px solid rgba(16, 49, 157, 0.4);
      }
      &.typing{
        outline: 3px solid rgba(16, 49, 157, 0.3);
        @include style_font(700, 1.8rem, 1, $color-2C99FF_azure);
      }
    }
    .btn-close{
      position: absolute;
      right: 2rem;
      top: 2rem;

      width: 2rem;
      height: 2rem;
    }
  }
  .search-banner.commu{
    label .icon-search.focused svg path{
      fill: $color-0AB4E2_commu-01;
    }
    input.typing{
      outline: 3px solid rgba(16, 49, 157, 0.3);
      @include style_font(700, 1.8rem, 1, $font_color-212121_grey);
    }
  }

  // * 💫️ 아이콘 
  .icon{
    background-repeat: no-repeat;
    background-size: 100%;
  }
  .icon-down{
    margin-left: 1rem;
    width: 3.4rem;
    height: 3.4rem;
    background-image: url('../../public/asset/images/icon/ico_down.svg');
  }
  .icon-open_in_new{
    margin-left: 4px;
    width: 1.5rem;
    height: 1.5rem;
    background-position: top;
  }
  // .icon-table_notice 
  .icon-table_new{
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    background-image: url('../../public/asset/images/icon/ico_table_new.svg');
  }
  .icon-more_arrow{
    width: 1.8rem;
    height: 1.1rem;
    margin-left: 5px;
    background-image: url('../../public/asset/images/icon/ico_more_arrow.svg');
  }

  .icon-table_f_ar_d{
    width: 2.6rem;
    height: 2.6rem;
    background-image: url('../../public/asset/images/icon/ico_table_foling_arrow_down.svg');
  }
  .icon-table_f_ar_r{
    width: 2.6rem;
    height: 2.6rem;
    background-image: url('../../public/asset/images/icon/ico_table_foling_arrow_right.svg');
  }
  .icon-table-p_ar_r{background-image: url('../../public/asset/images/icon/ico_table_page_arrow.svg');}
  .icon-table-p-ar_d{background-image: url('../../public/asset/images/icon/ico_table_page_arrow_double.svg');}
  
  .icon-close{background-image: url('../../public/asset/images/icon/ico_table_page_arrow.svg');}
  .icon-table-notice{
    width: 2.8rem;
    height: 2.8rem;
    background-image: url('../../public/asset/images/icon/ico_table_notice.svg');
  }
  .icon-mail{
    background-image: url('../../public/asset/images/icon/ico_property 1.svg');
  }
  .icon-sort{
    vertical-align: middle;
    width: 1.4rem;
    height: 1.6em;
    background-image: url('../../public/asset/images/icon/ico_sort.svg');
  }
  .icon-btn-table-list{
    width: 2.6rem;
    height: 2.6rem;
    background-image: url('../../public/asset/images/icon/ico_btn-table_list.svg');
  }
  .icon-commu-01{
    background-image: url('../../public/asset/images/icon/ico_01coding.svg');
  }
  .icon-commu-02{
    background-image: url('../../public/asset/images/icon/ico_02forum.svg');
  }
  .icon-commu-03{
    background-image: url('../../public/asset/images/icon/ico_03review.svg');
  }
  .icon-commu-04{
    background-image: url('../../public/asset/images/icon/ico_04qa.svg');
  }
  .icon-banner_ar_l{
    background-image: url('../../public/asset/images/icon/ico_banner_arrow_left.svg');
  }
  .icon-banner_ar_r{
    background-image: url('../../public/asset/images/icon/ico_banner_arrow_right.svg');
  }
  .icon-write{
    width: 2.4rem;
    height: 2.4rem;
    margin-left: 1rem;
    background-image: url('../../public/asset/images/icon/ico_write.svg');
  }

  // 게시판 icon set - 구름커뮤니티
  .commu{
    .icon-img{background-image: url('../../public/asset/images/icon/ico_insert_photo-commu.svg');}
    .icon-file{background-image: url('../../public/asset/images/icon/ico_insert_drive_file-commu.svg');}
    .icon-zip{background-image: url('../../public/asset/images/icon/ico_insert_drive_file_zip-commu.svg');}
    .icon-new{background-image: url('../../public/asset/images/icon/ico_insert_new-commu.svg');}
  }

  .logo{
    display: block;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }
  // * 🎈img 
  .img{
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .img-help-banner{
    position: absolute;
    bottom: 0;
    left: 13.4rem;
    width: 28.9rem;
    height: 35.7rem;
    background-image: url('../../public/asset/images/img_help_banner.png');
  }
  .img-no-result{
    background-image: url('../../public/asset/images/img_no_result.png');
  }
}

// *illust
.illust-main-customer{
  background-color: red;
}

// TEMP page 
div.helloTmax{
  width: 70vw;
  margin: 0 auto;
  text-align: center;
  h1{
    padding: 5rem 0;
    font-size: 3rem;
  }
  h2{
    padding: 2rem 0;
    line-height: 1.4em;
    font-size: 2rem;
  }
  p{
    padding: 2rem 0;
    font-size: 1.4rem;
    line-height: 1.6em;
  }
  ul{
    padding-top: 4rem;
    li{
      padding-top: 1rem;
      display: flex;
      justify-content: space-between;
      a{
        color: blue;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
  hr{
    margin: 0.4rem 0;
    background-color: darkblue;
  }
}