// 🏠️ OSP-7111 🏠️ OSP-7116 🏠️ OSP-7120 🏠️ OSP-7126

@import '../../modules';

.background-01{
  @include style_background-01();
}
// 공통
.main-osp7111,
.main-osp7113,
.main-osp7116,
.main-osp7117,
.main-osp7120,
.main-osp7126,
.main-osp7112{
  section{
    text-align: center;
    h2, h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 7.4rem;
    }
  }
  .width1352{
    width: 135.2rem;
    margin: 0 auto;
  }
  .search-table{
    margin-top: 7rem;
    margin-bottom: 2.4rem;
  }
  a.link-more{
    @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
    margin-top: 3.8rem;
    padding: 1rem 0;
    display: inline-block;
    i{
      margin-left: 6px;
    }
  }
  a.link-list{
    height: 5.8rem;
    padding: 1.4rem 4.4rem;

    border: 1px solid #B0B0B0;
    border-radius: 4rem;

    display: inline-flex;
    align-items: center;
    margin-top: 10.6rem;
    i.icon-btn-table-list{
      margin-right: 1rem;
    }
  }
  button.btn-no-result{
    width: 10.9rem;
    height: 3.4rem;
    text-align: center;
    padding-top: 0.4rem;
    padding-left: 0.4rem;
    margin: 0 2rem;
    border-radius: 5rem;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
    i.icon-write{
      margin-left: 0;
      background-image: none;
    }
  }
  div.no-result{
    position: relative;
    padding: 11.5rem 0;
    margin-bottom: 10.6rem;
    border-radius: 2rem;
    background-color: $bg_main-F8F9FA;
    .result-key{
      @include style_font(700, 2.8rem, 1, $color-2C99FF_azure);
      padding-bottom: 2rem;
    }
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    p{
      margin-top: 6.8rem;
      @include style_font(500, 1.8rem, 2em, $font_color-797979_grey);

    }
    button span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
    span.img-no-result{
      position: absolute;
      bottom: 0;
      left: -7.1rem;
      display: block;

      width: 20.4rem; 
      height: 29.8rem;
    }
  }
}

main.main-osp7111{
  position: relative;

  section.background-01{
    h2{
      padding-bottom: 9.6rem;
    }
    .bg_c-l{
      width: 45.5rem;
      background-size: 200%;
      background-position-y: 150%;
    }
    .bg_c-r{
      width: 113.7rem;
      background-size: 120%;
      background-position-y: -20%;
    }
    ul{
      display: flex;
      gap: 3.9rem;
      justify-content: center;
      z-index: 9;
      li button{
        width: 45.4rem;
        padding: 4rem 4.7rem;
        text-align: left;
        background: white;
        box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.05);
        border-radius: 3rem;
        i.icon{
          width: 10rem;
          height: 10rem;
          background-position-x: -1rem;
        }
        .icon-custom-01{
          background-image: url('../../../../public/asset/images/icon/ico_CS-01.svg');
        }
        .icon-custom-02{
          background-image: url('../../../../public/asset/images/icon/ico_CS-02.svg');
        }
        .icon-custom-03{
          background-image: url('../../../../public/asset/images/icon/ico_CS-03.svg');
        }
      
        h6{
          padding-top: 1.5rem;
          padding-bottom: 1.8rem;
          @include style_font(700, 2.8rem, 1.4em, #1f1f1f);
        }
        span{
          @include style_font(400, 2rem, 1.6em, #848484);
          width: 28rem;
        }
      }
    }
  }
  section.custom-prods{
    padding-top: 9.8rem;
    padding-bottom: 11.2rem;
    h3{
      padding-bottom: 9.8rem;
    }
    ul{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 3.9rem;
      button{
        padding-left: 4rem;
      }
      li, button{
        width: 45.4rem;
        height: 17.2rem;

        text-align: left;
        @include style_font(700, 3.6rem, 1, white);
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 3rem;
      }
    }
  }
  section.custom-notice{
    padding-top: 9.6rem;
    padding-bottom: 7.6rem;
    background-color: $bg_main-F8F9FA;
  }
  section.custom-question{
    padding-top: 9.6rem;
    padding-bottom: 5.6rem;
    background-color: $bg_main-F8F9FA;
    div.custom-board{
      padding-top: 0;
    }
  }
}

main.main-osp7116 div.custom-board{
  padding-top: 0;
  padding-bottom: 9.6rem;
  box-shadow: none;
  div.custom-board-title{
    @include board_grid_notice();
    span{
      height: 4rem;
      text-align: center;
      background-color: $bg_color-F8F8F8-grey;
      padding: 1rem 0;
      @include style_font(500, 1.6rem, 1.2em, $font_color-797979_grey);
      border-radius: 5px;
    }
  }
  ul.cont-ul{
    padding: 0;
  }
}

main.main-osp7120{
  div.search-table-table{
    padding-bottom: 2rem;
  }
  div.custom-board{
    padding-top: 0;

    box-shadow: none;
    padding-bottom: 9.6rem;
  }
  ul.cont-ul.question{
    padding: 0;
  }
}

main.main-osp7126{
  .width1218{
    width: 1218px;
    margin: 0 auto;
  }

  section.content{
    position: relative;
    padding-top: calc(9.6rem - 1em);
    div.background-01{
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
    }
    h2{
      line-height: 1.6em;
      strong{
        color: $color-2C99FF_azure;
      }
    }
    ul.products{
      z-index: 999;
      padding-bottom: 18rem;
      > li{
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 5.2rem 6.4rem 4.6rem 6.4rem;
        margin-bottom: 5rem;
        background: #FFFFFF;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
        border-radius: 30px;
      }
      li:first-child{
        div.download-cont{
          p{
            width: 60rem;
            padding-bottom: 3.6rem;

          }
        }
      }
      div.download-cont{
        text-align: left;
        flex: 2 2 auto;
        h4{
          height: 4rem;
          @include style_font(700, 3rem, 1, black);
        }
        p{
          @include style_font(400, 1.8rem, 2em, #212121);
          padding-top: 1.7rem;
        }
        div.tmaxgr-spec{
          @include style_font(700, 1.4rem, 1, #91989D);
          ul{
            padding-left: 4rem;
            display: inline-flex;
            flex-wrap: wrap;
            width: 80%;
            li{
              width: 50%;
              padding-bottom: 1.4rem;
              span.spec-tit{
                width: 6rem;
                font-weight: 700;
              }
              span{
                padding-left: 2.4rem;
                font-weight: 400;
              }
            }
          }
        } 
      }
      div.download-btn{
        a.btn{
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 20.8rem;
          padding: 1.1rem 1.9rem 1.1rem 2.4rem;
          margin-top: 2.4rem;
        }
        .icon-open_in_new{
          width: 1.6em;
          height: 2.4rem;
          svg{
            width: 1.2em;
            height: 2.4rem;
            path{
              fill: white;
            }
          }
        } 
      }
    }
  }
}

main.main-osp7112{
  padding-top: 8rem;
  text-align: center;
  padding-bottom: 13.6rem;
  div.search-result{
    height: 14.9rem;
    padding: 4rem 0;
    margin-bottom: 6rem;
    background-color: $bg_color-F8F8F8-grey;
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    span.search-key{
      @include style_font(700, 2.4rem, 1, $color-2C99FF_azure);
      margin-bottom: 2rem;
    }
  }
  div.custom-board{
    box-shadow: none;
    div.result{
      text-align: left;
      padding-bottom: 2.4rem;
      span{
        @include style_font(400, 2rem, 1, $font_color-212121_grey);
        &.res-cate{
          margin-right: 8px;
          font-weight: 700;
        }
      }
    }
    ul.cont-ul{
      padding: 0;
      &.question{
        padding-top: 0;
      }
    }
  }
  .search-notice{
    margin-bottom: 7.6rem;
  }
}

main.main-osp7113{
  padding-top: 10.6rem;
  padding-bottom: 13rem;
  text-align: center;
  .width1058{
    width: 105.8rem;
    margin: 0 auto;
  }

}

main.main-osp7119{
  padding-bottom: 10.6rem;
  text-align: center;

  .width1058{
    width: 105.8rem;
    margin: 0 auto;
  }
  div.top{
    height: 11.9rem;
    text-align: left;
