main.main-osp7117{
  padding-top: 7.9rem;
  padding-bottom: 10.2rem;
  text-align: center;
  .custom-board{
    box-shadow: none;
    padding: 0;
  }
  div.notice-board.--dp2{
    min-height: 70.2rem;
    background-color: $bg_main-F8F9FA;
    padding: 4.4rem 4.8rem;
    margin-bottom: 4.4rem;

    p{
      text-align: left;
      @include style_font(400, 2rem, 2em, #4b4b4b);
    }
  }
}